.tile {
    z-index: 10;

    &.style-1 {
        background: #555;
    }

    &.style-2 {
        background: #222;
    }
}

.tile-2 {
    z-index: 10;
    border: 1px solid transparent;

    &:before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        pointer-events: none;
        transform: scale(1);
        transition: 3.4s transform linear, 0.6s opacity linear, 0.4s background-color linear;
        opacity: 1;
    }

    &.style-1:before {
        background: #ccc;
    }

    &.style-2:before {
        background: #aaa;
    }

    &.mined {
        border: 0;
        z-index: 2000;
        transform: scale(5);

        &:before {
            background: red;
            transform: scale(5);
            border-radius: 100%;
        }
    }

    &.flagged {
        border: 0;

        &:before {
            background: green;
        }
    }

    &.hidden:before {
        opacity: 0;
    }

    &.bip:after {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -5px;
        margin-top: -7px;
        width: 10px;
        height: 15px;
        background: yellow;
        border: 1px solid #000;
    }

    &.shake:before,
    &.flagged:before {
        animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) infinite both;
        transform: translate3d(0, 0, 0);
        backface-visibility: hidden;
        perspective: 1000px;
    }

    &.microbial,
    &.flagged:before {
        background:
            radial-gradient(circle at 0% 50%, rgba(96, 16, 48, 0) 9px, #613 10px, rgba(96, 16, 48, 0) 11px) 0px 10px,
            radial-gradient(at 100% 100%,      rgba(96, 16, 48, 0) 9px, #613 10px, rgba(96, 16, 48, 0) 11px),
            #8a3;
        background-size: 20px 20px;
    }

    &.exit:after {
        content: "";
        position: absolute;
        width: 80%;
        height: 80%;
        top: 10%;
        left: 10%;
        border: 5px solid green;
        border-radius: 100%;
    }

    &.reached:after {
        background: green;
    }
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}
